<template>
  <div>
    <!--banner-->
    <div class="banner_index">
      <div class="banner_index_title">
        <span>{{showSize}}</span>
        <div class="banner_index_content"></div>
      </div>
      <div class="banner_index_icon">
        <svg class="l1" width="3.2rem" height="3.2rem">
          <path d="M160,0.5C212.9,53.4,319.5,160,319.5,160 L160,319.5L0.5,160L160,0.5z" style="stroke:#fff; stroke-width:1.2px;"></path>
        </svg>
        <!--<svg class="picSvgImg" width="3.2rem" height="3.2rem" xmlns="http://www.w3.org/2000/svg">-->
          <!--<g>-->
            <!--<image xlink:href="http://f10.baidu.com/it/u=107851725,1461296661&fm=76" x="160" y="160" height="100" width="100" >-->
              <!--<animateTransform attributeName="transform" begin="0s" dur="5s" type="rotate" from="0 160 160" to="360 160 160" repeatCount="indefinite"/>-->
            <!--</image>-->
          <!--</g>-->
        <!--</svg>-->
        <svg class="l2" width="3.2rem" height="3.2rem">
          <path d="M160,40.5C184,64.5,279.5,160,279.5,160 L160,279.5L40.5,160L160,40.5z" style="stroke:#fff; stroke-width:1.2px;"></path>
        </svg>
        <svg class="l3" width="3.2rem" height="3.2rem">
          <path d="M160,80.5c21.7,21.7,79.5,79.5,79.5,79.5 L160,239.5L80.5,160L160,80.5z" style="stroke:#fff; stroke-width:1.2px;"></path>
        </svg>
      </div>
    </div>
    <!--专业技能-->
    <div class="skill_index">
      <div class="skill_index_title">
        <i></i>
        <span>专业技能</span>
        <i></i>
      </div>
      <div class="skill_index_content">
        <div class="container">
          <section class="grid3d horizontal" id="grid3d">
            <div class="grid-wrap">
              <div class="grid">
                <figure><span class="zzc_bg"><span>Html5</span></span><img class="gridImg" src="../../assets/images/3.jpg" alt="img04"/></figure>
                <figure><span class="zzc_bg"><span>Css3</span></span><img class="gridImg" src="../../assets/images/4.jpg" alt="img03"/></figure>
                <figure><span class="zzc_bg"><span>JavaScript</span></span><img class="gridImg" src="../../assets/images/1.jpg" alt="img01"/></figure>
                <figure><span class="zzc_bg"><span>Vue</span></span><img class="gridImg" src="../../assets/images/5.jpg" alt="img05"/></figure>
                <figure><span class="zzc_bg"><span>UI Frame</span></span><img class="gridImg" src="../../assets/images/2.jpg" alt="img02"/></figure>
                <figure><span class="zzc_bg"><span>Plug-in Library</span></span><img class="gridImg" src="../../assets/images/8.jpg" alt="img08"/></figure>
              </div>
            </div><!-- /grid-wrap -->
            <div class="content">
              <div>
                <div class="dummy-img">111</div>
                <p class="dummy-text">111</p>
                <p class="dummy-text">111</p>
              </div>
              <div>
                <div class="dummy-img">222</div>
                <p class="dummy-text">222</p>
                <p class="dummy-text">222</p>
              </div>
              <div>
                <div class="dummy-img">333</div>
                <p class="dummy-text">333</p>
                <p class="dummy-text">333</p>
              </div>
              <span class="loading"></span>
              <span class="icon close-content"></span>
            </div>
          </section>
        </div><!-- /container -->
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'home',
  data () {
    return {
      bannerSize: 'Hello word , hello 2019',
      showSize: '',
      flySize: '这世界上，并不是所有的东西都符合想象，有些时候，山是水的故事，云是风的故事；也有些时候，星不是夜的故事，情不是爱的故事。但是，无奈但又必须去接受。因为幸福，其实很简单。平静的呼吸，仔细的聆听，微笑着生活；有人爱，有事做，有所期待；',
      flyShowSize: '',
      flyArr: []
    }
  },
  mounted () {
    this.oSize()
    this.flyIn()
    gridAnimate()
  },
  methods: {
    oSize () {
      let num = 1
      let myTimer = setInterval(() => {
        this.showSize = this.bannerSize.substring(0, num)
        num++
        if (this.showSize === this.bannerSize) {
          clearInterval(myTimer)
        }
      }, 100)
    },
    flyIn () {
      let flyNum = 0
      let flyTimer = setInterval(() => {
        const randomN = Math.floor(Math.random() * 4) + 1
        let eff = ''
        switch (randomN) {
          case 1:
            eff = 'fadeInRightBig'
            break
          case 2:
            eff = 'fadeInLeftBig'
            break
          case 3:
            eff = 'fadeInUpBig'
            break
          case 4:
            eff = 'fadeInDownBig'
            break
        }
        flyNum++
        const sizeList = this.flySize.substring(flyNum - 1, flyNum)
        this.flyShowSize = sizeList
        const oHtml = '<span style="display: inline-block;" class="introduce animated ' + eff + '">' + sizeList + '</span>'
        $('.banner_index_content').append(oHtml)
        if (flyNum === this.flySize.length) {
          clearInterval(flyTimer)
        }
      }, 20)
    }
  }
}
</script>
<style scoped>
  .banner_index{
    width: 100%;
    height: 5rem;
    background: url("../../assets/images/bannerBg.jpg") no-repeat;
    background-size: 100% 100%;
    position: relative;
  }
  .banner_index_icon{
    width: 3.5rem;
    height: 3rem;
    /*background: url("../../assets/images/imac_mock-up.png") no-repeat;*/
    /*background-size: 100% 100%;*/
    position: absolute;
    right: 10%;
    top: 50%;
    margin-top: -2rem;
  }
  @keyframes mymove
  {
    0% {right:0rem;top:50%;}
    25% {right:2rem;top:30%;}
    50% {right:4rem;top:50%;}
    75% {right:2rem;top:60%;}
    100% {right:0rem;top:50%;}
  }
  @-webkit-keyframes mymove /*Safari and Chrome*/
  {
    0% {right:0rem;top:50%;}
    25% {right:2rem;top:30%;}
    50% {right:4rem;top:50%;}
    75% {right:2rem;top:60%;}
    100% {right:0rem;top:50%;}
  }
  .banner_index_title{
    width: 50%;
    height: 100%;
  }
  .banner_index_title > span{
    width: 100%;
    display: inline-block;
    text-align: center;
    font: .4rem name_font_family_02;
    color: #ffffff;
    text-shadow: .01rem .04rem .02rem #333;
    margin-top: .6rem;
  }
  .banner_index_content{
    width: 50%;
    height: auto;
    margin: 0 auto;
    padding: .2rem;
    background: rgba(255,255,255,.1);
    border-radius: 6px;
    box-shadow: 0px 0px 10px #fff;
    margin-top: .4rem;
    overflow: hidden;
  }
  .skill_index{
    width: 100%;
    height: auto;
    background: url("../../assets/images/plate01Bg_02.jpg");
    position: relative;
    padding-bottom: .5rem;
  }
  .grid3d{
    width: 100%;
    padding-top: .5rem;
  }
  .grid figure{
    position: relative;
  }
  .grid figure, .grid .placeholder {
    width: 30%;
    height: 2.12rem;
    overflow: hidden;
  }
  .grid figure img{
    height: 100%;
    transition: all .5s;
    ransform: scale(1);
  }
  .grid figure .zzc_bg{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    background-color: rgba(20,133,239,.8);
    transform: translateX(-100%);
    transition: all .5s;
    display: inline-block;
    z-index: 999;
    text-align: center;
  }
  .grid figure .zzc_bg span{
    display: inline-block;
    line-height: 2.12rem;
    color: #ffffff;
    font: .6rem name_font_family_01;
    margin-top: .7rem;
  }
  .grid figure:hover .gridImg{
    transform: scale(1.3);
  }
  .grid figure:hover .zzc_bg{
    transform: translateX(0%);
  }

  .skill_index_title{
    width: 100%;
    text-align: center;
  }
  .skill_index_title span{
    display: inline-block;
    vertical-align: middle;
    margin-top: .1rem;
    font-size: 24px;
    color: #333333;
  }
  .skill_index_title i{
    display: inline-block;
    vertical-align: middle;
    width: .3rem;
    height: .02rem;
    background-color: rgba(0,0,0,.5);
    margin: .1rem .1rem 0 .1rem;
    animation:mymove 3s linear infinite alternate;
    -webkit-animation:mymove 3s linear infinite alternate; /*Safari and Chrome*/
  }
  @keyframes mymove
  {
    0% {transform:rotate(0deg) translate(0px,0px) scale(1);opacity: 1}
    50% {transform:rotate(180deg) translate(40px,0px) scale(0);opacity: 0}
    100% {transform:rotate(360deg) translate(0px,0px) scale(1);opacity: 1}
  }
  .skill_index_title p{
    font: 26px name_font_family_01;
  }
</style>



